import { BrowserRouter as Router } from "react-router-dom"
import RouterView from "./Router"
import GlobalStyle from "./globalStyles"
import { useTitle } from "./useHooks"
function App() {
  const path = window.location.pathname.slice(1)
  const title = {
    week1: "The F2E ",
    week2: "",
    week3: "Scrum ",
  }
  useTitle(title[path])
  return (
    <Router>
      <GlobalStyle />
      <RouterView />
    </Router>
  )
}

export default App
